body {
  font-family: 'Lobster', cursive; /* Change to a romantic cursive font */
  background-color: #ffdde1;
  display: flex;
  align-items: center;
  justify-content: center;
  height: 100vh;
  margin: 0;
  background: url('image.jpg') repeat;
  font-size: 35px;
  color: rgb(255, 0, 174);
  text-align: center;
}
.hh{
  -webkit-text-stroke: 2px #71004f;
  font-weight:900;

}
.pp{
  -webkit-text-stroke: 2px #71004f;

  font-size: 50px;
  font-style: oblique;
